select,
.dropbutton-widget {
  filter: grayscale(100%);
}

select {
  text-shadow: none !important;
}
select:hover, select:focus {
  color: initial !important;
}

.button {
  background: #f6f6f6;
  background: -webkit-linear-gradient(top, #f6f6f6, #e6e6e6);
  background: linear-gradient(to bottom, #f6f6f6, #e6e6e6);
  background-color: #f1f1f1;
}
.button:hover, .button:focus {
  background: #fcfcfc;
  background: -webkit-linear-gradient(top, #fcfcfc, #e8e8e8);
  background: linear-gradient(to bottom, #fcfcfc, #e8e8e8);
  background-color: #f8f8f8;
}
.button:active {
  background: #f6f6f6;
  background: -webkit-linear-gradient(top, #f6f6f6, #e6e6e6);
  background: linear-gradient(to bottom, #f6f6f6, #e6e6e6);
  background-color: #f1f1f1;
}

a.button--primary {
  color: #fff;
}

table .button:not(.button--primary),
.views-exposed-form .button:not(.button--primary) {
  background: #f6f6f6;
  background: -webkit-linear-gradient(top, #f6f6f6, #e6e6e6);
  background: linear-gradient(to bottom, #f6f6f6, #e6e6e6);
  background-color: #f1f1f1;
}
table .button:not(.button--primary):hover, table .button:not(.button--primary):focus,
.views-exposed-form .button:not(.button--primary):hover,
.views-exposed-form .button:not(.button--primary):focus {
  background: #fcfcfc;
  background: -webkit-linear-gradient(top, #fcfcfc, #e8e8e8);
  background: linear-gradient(to bottom, #fcfcfc, #e8e8e8);
  background-color: #f8f8f8;
}

#edit-actions-cancel,
.ui-dialog-buttonset .button:not(.button--primary),
#preview-submit {
  background: #f6f6f6;
  background: -webkit-linear-gradient(top, #f6f6f6, #e6e6e6);
  background: linear-gradient(to bottom, #f6f6f6, #e6e6e6);
  background-color: #f1f1f1;
}
#edit-actions-cancel:hover, #edit-actions-cancel:focus,
.ui-dialog-buttonset .button:not(.button--primary):hover,
.ui-dialog-buttonset .button:not(.button--primary):focus,
#preview-submit:hover,
#preview-submit:focus {
  background: #fcfcfc;
  background: -webkit-linear-gradient(top, #fcfcfc, #e8e8e8);
  background: linear-gradient(to bottom, #fcfcfc, #e8e8e8);
  background-color: #f8f8f8;
}

.content-header {
  background-color: #e7e7e7 !important;
  box-shadow: inset 0 -1px 0 0 #cccccc;
}
.content-header .page-title {
  padding-bottom: 1.25rem;
}

div:not(.entity-meta) .seven-details {
  background-color: #fcfcfc;
}

.entity-meta {
  background-color: #ededed !important;
}

.entity-meta__header,
.entity-meta .seven-details:not([open]) {
  background-color: #fcfcfc !important;
}

input.form-autocomplete,
input.form-text,
input.form-tel,
input.form-email,
input.form-url,
input.form-search,
input.form-number,
input.form-color,
input.form-file,
input.form-date,
input.form-time,
textarea.form-textarea,
fieldset:not(.fieldgroup) {
  background: #fcfcfc !important;
}

.system-modules div:not(.entity-meta) .seven-details {
  background-color: #fff;
}

.system-modules tr.even,
.system-modules tr.odd,
.locale-translation-status-form tr.even,
.locale-translation-status-form tr.odd {
  background: #f3f3f3 !important;
}

.system-status-counter {
  border-color: #e4e4e4;
  background: #fcfcfc;
}

.system-status-counter__status-icon {
  border-right-color: #e4e4e4;
  /* LTR */
  background-color: #f9f9f9;
}

[dir=rtl] .system-status-counter__status-icon {
  border-left-color: #e4e4e4;
}

.system-status-report__entry:last-of-type {
  border-bottom-color: #bebebe;
}

table h3 {
  font-size: 0.8125rem;
}
table thead th {
  border-color: #d4d4d4 !important;
  background: #f5f5f5 !important;
}
table tr {
  border-bottom-color: #e4e4e4;
}
table tbody tr {
  border-color: #e4e4e4;
}

.tabs.primary .tabs__tab {
  background-color: rgba(240, 240, 240, 0.7) !important;
}
.tabs.primary .tabs__tab:hover, .tabs.primary .tabs__tab:focus {
  background-color: #fafafa !important;
}
.tabs.primary .tabs__tab.is-active {
  border-color: #ccc !important;
  background-color: #fff !important;
}
.tabs.primary a:focus {
  text-decoration: none !important;
  background-color: #fafafa !important;
}

.is-horizontal .tabs {
  margin-top: 0 !important;
}

.tabs.secondary .tabs__tab + .tabs__tab {
  border-top-color: #d8d8d8 !important;
}

.is-collapse-enabled .tabs::before,
.is-horizontal .tabs::before {
  background-color: #ccc !important;
}

.password-strength__meter {
  background-color: #eaeaea !important;
}

.vertical-tabs {
  background: #e5e5e5;
}

.vertical-tabs__menu-item.is-selected {
  border-right-color: #fcfcfc;
  /* LTR */
  border-bottom-color: #b2b2b2;
}

[dir=rtl] .vertical-tabs__menu-item.is-selected {
  border-left-color: #fcfcfc;
}

.vertical-tabs__menu-item a {
  border-bottom-color: #b2b2b2;
  background-color: #f2f2f2;
}

.vertical-tabs__menu-item.is-selected a,
.vertical-tabs__menu-item a:hover,
.vertical-tabs__menu-item a:focus {
  background: #fcfcfc;
}

[data-vertical-tabs-panes] {
  background-color: #fcfcfc;
}

.vertical-tabs__panes {
  border-left-color: #a5a5a5;
  /* LTR */
}

[dir=rtl] .vertical-tabs__panes {
  border-right-color: #a5a5a5;
}

.views-display-top,
.form-edit .form-actions,
.view-preview-form__title {
  background-color: #e7e7e7;
}

#edit-display-settings-top .views-display-setting {
  background-color: #fff;
}

.views-ui-display-tab-bucket .views-display-setting:nth-of-type(2n) {
  background-color: #f4f4f4;
}

.views-ui-dialog .views-override:not(:empty) {
  background-color: #f3f3f3;
}

.views-ui-rearrange-filter-form .even td {
  background-color: #f3f3f3;
}

.ui-dialog .ui-dialog-titlebar {
  border-radius: 2px 2px 0 0 !important;
  background: #e7e7e7 !important;
}
.ui-dialog .ui-dialog-title {
  color: #333 !important;
}
.ui-dialog .ui-dialog-titlebar-close {
  border-color: #e7e7e7 !important;
  background: #888 !important;
}
.ui-dialog .ui-widget-content.ui-dialog-buttonpane {
  background: #f5f5f5 !important;
}

.ui-state-hover,
.ui-widget-content .ui-state-hover,
.ui-widget-header .ui-state-hover,
.ui-state-focus,
.ui-widget-content .ui-state-focus,
.ui-widget-header .ui-state-focus,
.ui-button:hover,
.ui-button:focus {
  font-weight: 600;
}

.layout-icon__region {
  fill: #f5f5f5;
}

.layout-region-node-footer__content {
  border-top-color: #bebebe;
}

.page-content {
  margin-bottom: 16px !important;
}

.content-footer {
  margin: 0 2.5em 4em;
}
.content-footer ul.menu {
  list-style: none;
  margin-left: 0;
  padding-top: 1em;
}
.content-footer ul.menu li.menu-item {
  display: inline;
  padding-right: 10px;
}

.cke_reset_all .cke_dialog_body textarea,
.cke_reset_all .cke_dialog_body input[type=text],
.cke_reset_all select.cke_dialog_ui_input_select {
  background: #fcfcfc;
}
.cke_reset_all select.cke_dialog_ui_input_select {
  background-color: #fcfcfc;
}
.cke_reset_all .cke_dialog .cke_dialog_footer {
  background: #f5f5f5;
}
.cke_reset_all a.cke_dialog_ui_button {
  background: #f6f6f6;
  background: -webkit-linear-gradient(top, #f6f6f6, #e6e6e6);
  background: linear-gradient(to bottom, #f6f6f6, #e6e6e6);
  background-color: #f1f1f1;
}
.cke_reset_all .cke_dialog_footer_buttons a.cke_dialog_ui_button:hover, .cke_reset_all .cke_dialog_footer_buttons a.cke_dialog_ui_button:focus {
  background: #fcfcfc;
  background: -webkit-linear-gradient(top, #fcfcfc, #e8e8e8);
  background: linear-gradient(to bottom, #fcfcfc, #e8e8e8);
  background-color: #f8f8f8;
}
.cke_reset_all .cke_dialog_footer_buttons a.cke_dialog_ui_button:active {
  background: #f6f6f6;
  background: -webkit-linear-gradient(top, #f6f6f6, #e6e6e6);
  background: linear-gradient(to bottom, #f6f6f6, #e6e6e6);
  background-color: #f1f1f1;
}

.maintenance-page {
  background-color: #dfdfdf;
}

@media all and (min-width: 48em) {
  /* 768px */
  .task-list .is-active {
    background: #eaeaea;
  }

  .task-list .is-active::after {
    border-left-color: #eaeaea;
    /* LTR */
  }

  [dir=rtl] .task-list .is-active::after {
    border-right-color: #eaeaea;
  }
}
.media-library-menu {
  background-color: #e5e5e5;
}

.media-library-menu__link {
  border-bottom-color: #b2b2b2;
  background-color: #f2f2f2;
}

.media-library-menu__link:active,
.media-library-menu__link:hover,
.media-library-menu__link:focus {
  background: #fcfcfc;
}

.media-library-menu__link.active {
  border-right-color: #fcfcfc;
  /* LTR */
  border-bottom-color: #b2b2b2;
}

[dir=rtl] .media-library-menu__link.active {
  border-left-color: #fcfcfc;
}

[dir=rtl] .media-library-content {
  border-right-color: #b2b2b2;
}

.media-library-add-form__input-wrapper {
  background: #fcfcfc;
}

h1,
h2 {
  font-weight: normal;
}